import React, { Component, useState, useEffect } from 'react';
import config from '../../common/config';

function Menu({ history, match: { path } }) {
    let renderMenuList = () => {
        let listNode = null;
        if(config.MENU.length) {
            listNode = config.MENU.map((menu) => {
                let { transKey, link, id, icon, iconActive } = menu;
                let activeClass = 'menu__list';
                let iconImage = icon;
                if(link === path) {
                    activeClass = 'menu__list active';
                    iconImage = iconActive;
                }
                let imageObj = {
                    className: "menu__icon",
                    style: {
                        backgroundImage: `url(${config.IMG_BASE}/icons/${iconImage}${config.IMG_EXT['svg']})`
                    }
                }
                return (
                    <li key={id} onClick={() => onClicktoRedirect(link)} className={activeClass}>
                        <div {...imageObj}></div>
                        <span>{transKey.toUpperCase()}</span>
                    </li>
                )
            });
        }
        return listNode;
    }

    let onClicktoRedirect = (url) => history.push(url);

    return (
        <ul className="menu">
            {renderMenuList()}
        </ul>
    );
}

export default Menu;